@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@200;400;800&display=swap");

html {
  font-size: 62.5%;
}

html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #010101;
  color: whitesmoke;
  font-family: "Outfit", sans-serif;
  height: 100vh;
}

.page {
  width: 100vw;
}

nav {
  display: flex;
  justify-content: space-around;
  max-height: 10rem;
  padding: 1rem 0;
}

#logo {
  height: 100%;
  padding: 0 6rem;
}

img:hover {
  cursor: pointer;
}

nav {
  justify-content: space-between;
  position: fixed;
  z-index: 99;
  top: 0;
  background-color: #010101;
  width: 100vw;
}

nav ul {
  display: flex;
  width: 60%;
  gap: 5rem;
  list-style: none;
  margin: 0;
  padding: 0 5rem;
}

nav ul li {
  align-self: center;
  font-size: 1.6rem;
}

nav ul li:hover {
  color: greenyellow;
  cursor: pointer;
}

.btn {
  width: 100px;
  font-weight: 800;
  background-color: greenyellow;
  border: 0.1rem solid greenyellow;
}
.btn:hover {
  cursor: pointer;
  color: #111;
  background-color: greenyellow;
}

section {
  margin-top: 12rem;
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  width: 100%;
  justify-content: center;
}

footer {
  text-align: center;
  justify-content: center;
  position: relative;
  padding: 2rem;
}
